import Header from "../components/Header"
import Footer from "../components/Footer"

const About = ({ user, isLoggedIn }) => {
    return (
        <div>
            <Header user={user} isLoggedIn={isLoggedIn} />
            <h5>Sobre</h5>
            <hr></hr>
            <p style={{boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', color: 'white', textAlign: 'justify', backgroundColor: 'dodgerblue',padding: '25px',borderRadius: '5px'}}>
                <strong><span style={{fontSize: '32px'}}>ARENDAR EM TETE</span></strong> é uma plataforma electrónica orientada a promoção imobiliária de residências localizadas nos bairros urbanos da cidade de Tete.  Foi desenvolvida com o intuito de intermediar clientes e proprietários que desejam alugar ou arrendar patrimónios.
                A plataforma oferece soluções flexíveis e interfaces intuitivas que permitem aos proprietários:
                <ul style={{marginLeft: '30px', marginTop: '10px'}}>
                    <li>Anunciar seus patrimónios;</li>
                	<li>Fazer uso de serviços de alerta de solicitações por SMS;</li>
                	<li>Anunciar conteúdos publicitários de produtos ou marcas.</li>
                </ul>
            </p>
            <Footer />
        </div>
    )
}

export default About 